body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  
}
h1, h2, h3, h4 {
  font-family: 'Titillium Web', sans-serif;
    letter-spacing: 0.1em;
}
h1{
  font-weight: 700;
  color: #b8ae58;
}

.article-lis-title {
  text-decoration: none;
  color: #b8ae58;
}

.article_lis_topic{
    color: #9e9649;
}
.articles_lis_info{
  font-size: 0.8em;
  color: #555;
}
.info_message{
  /* border: 1px dotted #eee; */
  font-style: italic;
  font-weight: 300;
  color: #bbb;
}

date{
  padding: 0em 0em 1em 0em;
  font-weight: 300;
  
}

button{
  
  font-family: 'Open Sans', sans-serif !important;
  font-weight: 300;
  text-transform: uppercase;
  font-size: 0.8em;
  letter-spacing: 0.4em;
  color: #fff;

  padding: 1em 1em 1em 1em;
  margin: 0.5em 0.5em 0.5em 0.5em;

  background: #b8ae58;
  border: 0px solid red;
  border-radius: 0.25em;

 
}

button:active{
  background: rgb(172, 229, 231);
}

button:hover{
  background: #8c9232;
}

.button_small{
  font-size: 0.6em;
 
}


input{
  padding: 0.9em 0.9em 0.9em 0.9em;
  margin: 0.5em 0.5em 0.5em 0.5em;
  border-radius: 0.25em;
  border: 1px solid #b8ae58;
}





.container{
    margin: 0.5em 0.5em 0.5em 0.5em;    /* flex-grow: 1; */

    display: -webkit-flex;

    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-content: space-between;
            align-content: space-between;
 
}
.image {
    /* flex-grow: 0; */

}
.text_part{
    text-align: left;
margin: 0.5em 0.5em 0.5em 0.5em;    /* flex-grow: 1; */
}
 img {
     height: 50vh;
}
.article_author_container {
  
  display: -webkit-flex;
  
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding: 1em 0em 0em 0em;
  
}
.avatar_big {
 
  
  display: -webkit-flex;
 
  
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;

}
.avatar_big img {
  height: 2em;


  
}
.article_author_name {
  font-size: 1em;
  padding: 0em 1em 0em 1em;
  color: rgb(24, 24, 24);

 
  -webkit-flex-grow: 0;

 
          flex-grow: 0;
}
.vote  {
    background: #fff; 
    color:  #b8ae58;
    font-weight: 600;
    font-size: 0.em;
    padding: 0.5em 0.5em 0em 0.9em;
    /* border: 1px solid #e9df89; */
}

.vote:hover{
    background: #fff; 
    color:  #999146;
}
.votes_number{
    color: #b8ae58;
}

.comment_author_date{
    font-weight: 300;
    font-size: 0.8em;
    color: #555;
}
.fas{
    margin: 0em 0.5em 0em 0.75em;
}
.comment_box{
    font-size: 0.9em;
    
    font-weight: 400;
    color :#555;
}
.error_message{
    display: -webkit-flex;
    display: flex;
    padding: 0.5em 0.5em 0.5em 0.5em;
    border: 1px solid #ccc;
    border-radius: 0.5em;
    background: #eee;

    font-size: 0.75em;
}

.fail {
    color: rgb(172, 120, 111);
    border: 1px solid  rgb(172, 120, 111);
    background: rgb(241, 221, 195);
}

.success {
    color: olive;
    border: 1px solid olive;
    background: rgb(206, 221, 175);
}
textarea{
    width: 80vw;
    margin: 0.5em;
}




.login_message{
    display: -webkit-flex;
    display: flex;
    padding: 0.5em 0.5em 0.5em 0.5em;
    border: 1px solid #ccc;
    border-radius: 0.5em;
    background: #eee;
}

.form_login{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.label_login{
    display: -webkit-flex;
    display: flex;
    margin: 0.5em 0.5em 0.5em 0.5em;
}


@media only screen and (max-width: 768px) {

.form_login{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 100vw;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    border: 1px solid #ccc;
}
}
